import Mock from 'mockjs'
export default {
  getMenu: config => {
    const { username, password } = JSON.parse(config.body)
    // 先判断用户是否存在
    // 判断账号和密码是否对应
    if (username === 'admin' && password === 'admin') {
      return {
        code: 20000,
        data: {
          menu: [
            {
              path: '/home',
              name: 'home',
              label: '首页',
              icon: 's-home',
              url: 'home/index'
            },
            {
              path: '/course',
              name: 'course',
              label: '课程管理',
              icon: 'notebook-1',
              url: 'course/index'
            },
            {
              path: '/grade',
              name: 'grade',
              label: '成绩管理',
              icon: 's-marketing',
              url: 'grade/index'
            },
            {
              label: '题目管理',
              icon: 'edit-outline',
              path: '/subject',
              children: [
                {
                  path: '/totalSubject',
                  name: 'totalSubject',
                  label: '学校题库',
                  icon: 'tickets',
                  url: 'subject/totalSubject'
                },
                {
                  path: '/majorSubject',
                  name: 'majorSubject',
                  label: '专业题库',
                  icon: 'tickets',
                  url: 'subject/majorSubject'
                }
              ]
            },
            {
              label: '试卷管理',
              icon: 'document-copy',
              path: '/paper',
              children: [
                {
                  path: '/showPaper',
                  name: 'showPaper',
                  label: '试卷列表',
                  icon: 'document-copy',
                  url: 'paper/showPaper'
                },
                {
                  path: '/addPaper',
                  name: 'addPaper',
                  label: '添加试卷',
                  icon: 'document-copy',
                  url: 'paper/addPaper'
                }
              ]
            },
          ],
          token: Mock.Random.guid(),
          message: '获取成功'
        }
      }
    } else if (username === 'xiaoxiao' && password === 'xiaoxiao') {
      return {
        code: 20000,
        data: {
          menu: [
            {
              path: '/',
              name: 'home',
              label: '首页',
              icon: 's-home',
              url: 'home/index'
            },
            {
              path: '/video',
              name: 'video',
              label: '商品管理',
              icon: 'video-play',
              url: 'mall/index'
            }
          ],
          token: Mock.Random.guid(),
          message: '获取成功'
        }
      }
    } else {
      return {
        code: -999,
        data: {
          message: '密码错误'
        }
      }
    }

  }
}